function returnWhole(length) {
  var stars = [];
  for(var i = 0; i < length; i++) {
      stars.push(<Icon name="star" className="style-star"/>);
  };
  return stars;
};

function returnHalf(length) {
  var stars = [];
  for(var i = 0; i < length; i++) {
      stars.push(<Icon name="star half" className="style-star"/>);
  };
  return stars;
};


